/* [Object] Modal
 * =============================== */
.modal {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  text-align: left;
  background: rgba(0, 0, 0, 0.9);
  transition: opacity 0.25s ease;
}

.modal__bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  cursor: pointer;
}

.modal-state {
  display: none;
}

.modal-state:checked + .modal {
  opacity: 1;
  visibility: visible;
}

.modal-state:checked + .modal .modal__inner {
  top: 0;
}

.modal__inner {
  transition: top 0.25s ease;
  position: absolute;
  top: -20%;
  right: 0;
  bottom: 0;
  left: 0;
  width: 75%;
  margin: auto;
  overflow: auto;
  background: #fff;
  border-radius: 0.5rem;
  height: 75%;
}

.modal__close {
  position: absolute;
  right: 1em;
  top: 1em;
  width: 1.1em;
  height: 1.1em;
  cursor: pointer;
}

.modal__close:after,
.modal__close:before {
  content: "";
  position: absolute;
  width: 2px;
  height: 1.5em;
  background: $secondaryColor;
  display: block;
  transform: rotate(45deg);
  left: 50%;
  margin: -3px 0 0 -1px;
  top: 0;
  z-index: 9;
}

.modal__close:hover:after,
.modal__close:hover:before {
  background: #aaa;
}

.modal__close:before {
  transform: rotate(-45deg);
}

@media screen and (max-width: 768px) {
  .modal__inner {
    width: 90%;
    height: 90%;
    box-sizing: border-box;
  }
}

.modalContainer {
  height: 100%;
  display: flex;
  flex-direction: column;
  &--title {
    background: $primaryColor;
    color: $secondaryColor;
    @include center-flex;
    flex: 0 1 8rem;
  }
  &--body {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    flex: 1 1 auto;
    & :not(.modalContainer--body--companies){
      z-index: 2;
    }
    &--companies {
      justify-content: space-evenly;
      align-items: center;
      position: absolute;
      display: flex;
      opacity: 0.3;
      flex-wrap: wrap;
      width: 100%;
      height: 80%;
      z-index: 1;
      & img {
        margin: 2rem;
      }
    }
  }
}
