//use of keyframes mixin
@include keyframes(opacityHide){
    0%{
        opacity:1;
        visibility: visible;
    }
    100%{
        opacity: 0;
        visibility: hidden;
    }
}

//use of keyframes mixin
@include keyframes(opacityShow){
    0%{
        opacity: 0;
        visibility: hidden;
    }
    100%{
        opacity:1;
        visibility: visible;
    }
}